export default {
    name:"activity-flow_count_down",
    props:['time'],
    computed:{
        newTime() {
            return {hours:this.addZero(this.time.hours),minutes:this.addZero(this.time.minutes),seconds:this.addZero(this.time.seconds)}
        }
    },
    methods: {
        addZero(num) {
            var aa = num.toString();
            if(aa.length === 1) {
                return '0'+aa;
            }else {
                return aa;
            }
        }
    },
}



